  .privacy-notice {
    background-color: rgba($mijug-blue, 0.1);
    border: 1px solid $mijug-blue-light;
    border-radius: 6px;
    padding: 1rem;
    margin-top: 2rem;

    h4 {
      color: $mijug-blue-dark;
      margin-top: 0;
      margin-bottom: 0.5rem;
      font-size: 1rem;
    }

    p {
      margin: 0;
      color: $grey-color-dark;
      font-size: 0.9rem;
      line-height: 1.4;
    }
  }

  // Responsive adjustments
  @include media-query($on-palm) {
    padding: 1rem;

    .info-grid {
      grid-template-columns: 1fr;
      gap: 1rem;
    }

    .info-card {
      padding: 1rem;

      .info-item {
        strong {
          display: block;
          margin-bottom: 0.25rem;
          min-width: auto;
        }
      }
